nav {
  width: 100%;
  height: 60px;
  background-color: #f05a22;
  color: white;
  display: flex;
  align-items: center;
  padding: 0 30px;
}
.nav-image {
  width: 300px;
  height: 45px;
  margin: 0 auto 0 auto;
}
@media screen and (max-width: 600px) {
  .nav-image {
    display: none;
  }
}
@media screen and (min-width: 600px) and (max-width: 768px) {
  .nav-image {
    display: none;
  }
}
